@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  background-color: #ababab;
  color: #1f1f1f;
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
}
.new-expense__control {
  font-size: 18px;
  padding: 0 20px 20px 10px;
}
.new-expense__control label {
  padding-right: 10px;
}
.new-expense__control input {
  width: 240px;
  height: 35px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  padding: 5px 10px;
}
.new-expense__btns {
  margin: 80px 25px 0 auto;
}
.new-expense__btns button {
  margin: 0 10px;
  padding: 15px 25px;
  border: 0px solid white;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 700;
  background-color: purple;
  color: white;
  box-shadow: 2px 2px rgb(255 255 255);
  cursor: pointer;
}
.hidden {
  display: none;
}

.new-expense {
  display: flex;
  width: 720px;
  margin: 20px auto;
  background-color: violet;
  padding: 20px 10px 20px 20px;
  border-radius: 20px;
  box-shadow: 4px 4px rgb(115 115 115);
}
.show-form-btn {
  margin: 0 auto;
  padding: 15px 25px;
  border: 0px solid white;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 700;
  background-color: purple;
  color: white;
  box-shadow: 2px 2px rgb(255 255 255);
  cursor: pointer;
}
.hidden {
  display: none;
}
.reveal {
  display: block;
}

.card {
  border-radius: 20px;
  box-shadow: 4px 4px rgb(115 115 115);
}

.chart {
  height: 10rem;
  background: grey;
  border-radius: 20px;
  display: flex;
  justify-content: space-around;
  padding: 10px;
  box-shadow: 3px 3px #bfbfbf;
}

.bar-chart {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.bar-chart__inner {
  height: 100%;
  width: 20px;
  background: white;
  border-radius: 20px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.bar-chart__fill {
  background: violet;
  border-radius: 20px;
}
.bar-chart__label {
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 5px;
}

.expenses-filter {
  padding: 0;
  position: relative;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding: 0 15px;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  -webkit-appearance: none;
          appearance: none;
  cursor: pointer;
}
.expenses-filter__select select {
  -webkit-appearance: none;
          appearance: none;
}
.expenses-filter__select {
  position: relative;
}
.expenses-filter__select::after {
  content: "\25b6";
  position: absolute;
  top: 9px;
  right: 5px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.expense-date {
  border: 1px solid #2e2e2e;
  border-radius: 10px 0 0 10px;
  padding: 10px 0 4px 0;
  text-align: center;
  background-color: #fff;
  width: 110px;
}
.expense-date__month {
  font-size: 16px;
}
.expense-date__year {
  font-size: 12px;
}
.expense-date__day {
  font-size: 35px;
  font-weight: 900;
  margin-top: -5px;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4a4a4a;
  padding-right: 20px;
  margin: 15px 0;
  box-shadow: 3px 3px #bfbfbf;
}
.expense-description {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-left: 60px;
  color: white;
  font-size: 16px;
}
.expense-description__amount {
  background-color: #800080;
  padding: 10px 25px;
  border: 2px solid #fff;
  border-radius: 20px;
  font-size: 26px;
  font-weight: 900;
}

.expenses-list {
  list-style: none;
  padding: 0;
}
.expenses-list__fallback {
  text-align: center;
  padding-top: 40px;
}

.expenses {
  max-width: 720px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f5f5dc;
}

